<template>
  <div>
    <el-row style="margin-bottom:20px;">
      <el-card shadow="always">
        <span class="content-title">二级解决方案管理</span>
        <el-button
          type="success"
          @click="openAddDialog()"
        >新增</el-button>
      </el-card>
    </el-row>
    <el-table
      border
      :data="tableData"
    >
      <el-table-column
        prop="index"
        label="序号"
        align="center"
        width="60"
      >
      </el-table-column>
      <el-table-column
        prop="title"
        label="标题"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="icon_src"
        label="logo图案"
        align="center"
        width="110"
      >
        <template slot-scope="scope">
          <img
            :src="scope.row.icon_src"
            alt=""
            style="width: 50px;height: 50px"
          >
        </template>
      </el-table-column>
      <el-table-column
        prop="status_name"
        label="状态"
        align="center"
        width="90"
      >
      </el-table-column>
      <el-table-column
        prop="link"
        label="链接"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="release_time"
        label="发布时间"
        align="center"
        width="160"
      >
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
        width="250"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.$index, scope.row)"
          >编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
          >删除</el-button>
          <!--<el-button
            size="mini"
            @click="handleStatus(scope.$index, scope.row)"
            v-if="scope.row.status"
          >取消显示</el-button>
          <el-button
            size="mini"
            @click="handleStatus(scope.$index, scope.row)"
            v-else
          >显示</el-button>-->
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="新增"
      :visible.sync="dialogVisible"
      width="60%"
      top="5vh"
      :append-to-body='true'
      :before-close="handleClose"
      :closeOnClickModal='false'
    >
      <div class="el-dialog-news">
        <el-form
          label-position="top"
          :rules="rules"
          ref="formLabelAlign"
          label-width="80px"
          :model="formLabelAlign"
        >
          <el-form-item
            label="标题"
            prop="title"
          >
            <el-input v-model="formLabelAlign.title"></el-input>
          </el-form-item>
          <el-form-item
            label="logo图标"
            prop="ico_name"
          >
            <el-col :span="20">
              <el-input
                v-model="formLabelAlign.ico_name"
                :disabled="true"
              ></el-input>
            </el-col>
            <el-upload
              class="upload-demo"
              action="core/file/upload"
              multiple
              :limit="1"
              :before-upload="beforeUpload"
              :show-file-list='false'
            >
              <el-button
                size="medium"
                type="primary"
              >点击上传</el-button>
            </el-upload>
          </el-form-item>
          <el-form-item label="状态">
            <el-radio-group v-model="formLabelAlign.status">
              <el-radio :label="0">不显示</el-radio>
              <el-radio :label="1">显示</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item
            label="发布时间"
            prop="release_time"
          >
            <el-date-picker
              v-model="formLabelAlign.release_time"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss"
              placeholder="选择日期"
              default-time="01:01:01"
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item label="链接">
            <el-input v-model="formLabelAlign.link"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="handleClose">取 消</el-button>
        <el-button
          type="primary"
          @click="addNewsInfo('formLabelAlign')"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import { authToken, authName } from "@/config";
import { storageHelper, getCurTime, utcTimeChange } from "@/utils";
import { prefix } from '@/api';
export default {
  data() {
    return {
      solution_id: 0,
      auth_name: "",
      //表格
      tableData: [],
      //弹出框
      dialogVisible: false,
      //表单
      formLabelAlign: {
        title: "",
        icon: "",
        ico_name: "",
        status: 1,
        release_time: "",
        link: ""
      },
      rules: {
        title: [{ required: true, message: "请输标题", trigger: "blur" }],
        release_time: [{ required: true, message: "请选择时间", }],
      },
      token:'',
      loading:null,
    };
  },
  mounted() {
    this.solution_id = this.$route.query.id;
    this.auth_name = storageHelper.getItem(authName, "healthcare");
    this.token = storageHelper.getItem(authToken, "healthcare");
    this.getSolutionList();
  },
  methods: {
    handleClose() {
      var that = this;
      this.$confirm('是否保存？',{
        showClose:false
      })
      .then(() => {
        that.addNewsInfo('formLabelAlign');
      })
      .catch(() => {this.dialogVisible = false});
    },
    openAddDialog() {
      this.formLabelAlign = {
        title: "",
        icon: "",
        ico_name: "",
        status: 1,
        release_time: "",
        link: ""
      };
      this.dialogVisible = true;
    },
    beforeUpload(file) {
      var that = this;
      var fd = new window.FormData();
      fd.append("file", file);
      axios
        .post("core/file/upload", fd, {
          headers: {
            "Content-Type": "multipart/form-data",
            token: this.token
          }
        })
        .then(function(res) {
          if (res.code == 0) {
            var url = res.data.url;
            that.formLabelAlign.ico_name = file.name;
            that.formLabelAlign.icon = res.data.code;
            that.$message({
              type: "success",
              message: "上传成功！"
            });
          }
          //console.log(res);
        });
      return false; // 返回false不会自动上传
    },
    showLoading(){
      this.loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
    },
    closeLoading(){
      if(this.loading){
        this.loading.close();
      }
    },
    //编辑
    handleEdit(index, row) {
      this.dialogVisible = true;
      this.formLabelAlign = {
        id: row.id,
        title: row.title,
        icon: row.icon,
        ico_name: row.ico_name,
        status: row.status,
        release_time: row.release_time,
        link: row.link
      };
    },
    //删除
    handleDelete(index, row) {
      var that = this;
      this.$confirm("是否确认删除该条信息?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
          var params = { id: row.id };
          that.submitSolutionInfo("content/solution/updateCaseStatus", params, "删除成功!");
      }).catch(() => {});
    },
    handleStatus(index, row) {
      var params = {id: row.id };
      this.submitSolutionInfo("content/solution/updateCaseStatus", params, "操作成功!");
    },
    //编辑框提交按钮
    addNewsInfo(formType) {
      var that = this;
      this.showLoading();
      this.$refs[formType].validate(valid => {
        if (valid) {
          var method = "";
          var info = "";
          if (that.formLabelAlign.hasOwnProperty("id")) {
            method = "content/solution/updateCase";
            info = "修改成功!";
          } else {
            method = "content/solution/createCase";
            info = "添加成功!";
          }
          that.formLabelAlign.solution_id = this.solution_id;
          that.submitSolutionInfo(method, that.formLabelAlign, info);
        } else {
          that.closeLoading();
          return false;
        }
      });
    },
    getEditorContent(content) {
      this.formLabelAlign.content = content;
    },
    getSolutionList() {
      var params = {
        solution_id: this.solution_id,
      };
      var that = this;
      axios.get("content/solution/caseList", { params }).then(
        function(response) {
          var code = response.code;
          if (0 == code) {
            var array = [];
            var json = response.data;
            for (var i = 0; i < json.length; i++) {
              var status = json[i].status;
              var icon_src = "";
              var ico_name = "";
              if (null != json[i].icon_info) {
                icon_src = json[i].icon_info.src;
                ico_name = json[i].icon_info.name;
              }
              json[i].release_time = utcTimeChange(json[i].release_time);
              var status_name = "显示";
              if (status == 0) {
                status_name = "未显示";
              }
              json[i].index = i + 1;
              json[i].status_name = status_name;
              json[i].icon_src = icon_src;
              json[i].ico_name = ico_name;
              array.push(json[i]);
            }
            that.tableData = array;
          } else {
            //console.log(code);
          }
        },
        function(error) {
          //console.log(error);
        }
      );
    },
    submitSolutionInfo(method, params, info) {
      var that = this;
      axios.post(method, params).then(
        function(response) {
        that.closeLoading();
          var code = response.code;
          if (0 == code) {
            that.$message({
              type: "success",
              message: info
            });
            that.dialogVisible = false;
            that.getSolutionList();
          } else {
            that.$message({
              type: "success",
              message: "操作失败:" + response.message
            });
          }
        },
        function(error) {
          that.closeLoading();
        }
      );
    },
  }
};
</script>

<style scoped>
.content-title {
  margin-right: 20px;
}
.advanced-box {
  padding: 20px 10px;
  margin-top: 15px;
  background-color: #f7f7f7;
}
.pager {
  float: right;
  padding: 10px 0;
}
.el-dialog__header {
  border-bottom: 1px solid #ccc;
}
.el-dialog-news {
  height: 530px !important;
  padding-right: 15px;
  overflow-y: scroll;
}
.el-dialog__footer {
  border-top: 1px solid #ccc;
}
.el-form-item__label {
  padding-bottom: 0 !important;
}
</style>


